import React from "react";
import Swiper from "react-id-swiper";
import { compose, pure, withProps, withHandlers } from "recompose";

const SwiperApp = ({ data, params, renderItem }) => {
  return <Swiper {...params}>{data.map(renderItem)}</Swiper>;
};

export default compose(
  withProps(() => {
    const params = {
      effect: 'coverflow',
      grabCursor: true,
      centeredSlides: true,
      slidesPerView: 'auto',
      coverflowEffect: {
        rotate: 50,
        stretch: 0,
        depth: 100,
        modifier: 1,
        slideShadows: true
      },
      pagination: {
        el: '.swiper-pagination'
      },
      loop: true,
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev"
      },
      breakpoints: {
        // when window width is <= 320px
        320: {
          slidesPerView: 1,
          spaceBetween: 10
        },
        // when window width is <= 480px
        480: {
          slidesPerView: 2,
          spaceBetween: 20
        },
        // when window width is <= 640px
        640: {
          slidesPerView: 2,
          spaceBetween: 20
        },
        1920: {
          slidesPerView: 4,
          spaceBetween: 20
        }
      }
    };

    const data = [
      {
        name: "Google",
        link: "www.google.com"
      },
      {
        name: "Amazon",
        link: "www.amazon.com"
      },
      {
        name: "Facebook",
        link: "www.facebook.com"
      },
      {
        name: "Yahoo",
        link: "www.yahoo.com"
      },
      {
        name: "Mircrosoft",
        link: "www.microsoft.com"
      },
      {
        name: "Apple",
        link: "www.apple.com"
      }
    ];

    return {
      params,
      data
    };
  }),
  withHandlers({
    renderItem: () => (item, index) => (
      <div key={index}>
        <a href={item.link}>{item.name}</a>
      </div>
    )
  }),
  pure
)(SwiperApp);
